/* ==========================================================================
   Property shortcode style - begin
   ========================================================================== */

.qodef-property-list-holder {
    @include qodefRelativeHolderLayout();

	/***** Article Global Style - begin *****/

    article {
	    @include qodefRelativeHolderLayout();
        box-sizing: border-box;

	    .touch & {
		    cursor: pointer;
	    }

	    .qodef-pl-item-inner {
		    @include qodefRelativeHolderLayout();
	    }
	    
	    .qodef-pli-image {
		    @include qodefRelativeHolderLayout();
		    
		    img {
			    display: block;
			    width: 100%;
		    }
	    }

	    .qodef-pli-link {
		    @include qodefAbsoluteHolderLayout();
	    }

	    .qodef-pli-text-wrapper {
	        @include qodefTableLayout();
	    }

	    .qodef-pli-text {
		    @include qodefTableCellLayout();

		    .qodef-pli-title {
				margin: 0;
		    }

		    .qodef-pli-category-holder {
			    position: relative;
			    display: block;
			    margin: 3px 0 5px;

			    a {
				    position: relative;
				    display: inline-block;
				    vertical-align: middle;
				    padding: 0 6px 0 0;
				    margin: 0 3px 0 0;
				    z-index: 8;
				    
				    &:after {
					    position: absolute;
					    top: 0;
					    right: 0;
					    content: ',';
					    color: inherit;
					    font-size: 12px;
					    line-height: inherit;
				    }

				    &:last-child {
					    margin: 0;
					    padding: 0;
					    
					    &:after {
						    display: none;
					    }
				    }
			    }
		    }

		    .qodef-pli-excerpt {
			    margin: 3px 0 0;
		    }
	    }
    }

	/***** Article Global Style - end *****/
	
	/***** Specific Global Style - begin *****/
	
	&.qodef-pl-has-shadow {
		
		article {
			
			.qodef-pli-image {
				box-shadow: $default-box-shadow;
			}
		}
	}
	
	&.qodef-pl-has-filter {
		
		.qodef-pl-inner {
			overflow: hidden;
		}
	}
	
	&.qodef-pl-no-content {
		
		.qodef-pli-text-holder {
			display: none;
		}
	}
	/***** Specific Global Style - end *****/
	
	/***** Property Types - begin *****/
	
	&.qodef-pl-gallery {
		
		&:not(.qodef-pl-one-column) {
			
			article {
				float: left;
			}
		}
		
		$gallery_columns_number: ('two', 'three', 'four', 'five');
		
		@for $i from 0 to length($gallery_columns_number) {
			&.qodef-pl-#{nth($gallery_columns_number,$i+1)}-columns {
				
				article {
					width: 100% / ($i+2);
				}
				
				@if ($i > 1) { // set different break point for four and five columns
					@media only screen and (min-width: $laptop-landscape-medium-plus-pixel) {
						
						article {
							
							&:nth-child(#{$i+2}n+1) {
								clear: both;
							}
						}
					}
				} @else {
					@media only screen and (min-width: $ipad-landscape-plus-pixel) {
						
						article {
							
							&:nth-child(#{$i+2}n+1) {
								clear: both;
							}
						}
					}
				}
			}
		}
	}
	
	&.qodef-pl-masonry {
		
		.qodef-pl-inner {
			opacity: 0;
		}
		
		article,
		.qodef-pl-grid-sizer {
			width: 100%;
		}
		
		.qodef-pl-grid-gutter {
			width: 0;
		}
		
		$masonry_columns_number: ('two', 'three', 'four', 'five');
		
		@for $i from 0 to length($masonry_columns_number) {
			&.qodef-pl-#{nth($masonry_columns_number,$i+1)}-columns {
				$column_width: 100%/($i+2);
				
				article,
				.qodef-pl-grid-sizer {
					width: $column_width;
				}
				
				article {
					
					&.qodef-pl-masonry-large-width,
                    &.qodef-pl-masonry-large-width-height {
						width: $column_width * 2;
					}
				}
			}
		}

        &.qodef-pl-images-fixed {
	        
            article {
	            
                .qodef-pl-item-inner {
                    height: 100%;
                }
	
	            .qodef-pli-image {
		            height: 100%;
		
		            img {
			            height: 100%;
		            }
	            }
            }
        }
	}

	/***** Property Types - end *****/

	/***** Additional Features - begin *****/

	&.qodef-pl-has-animation {

		article {
			opacity: 0;
			transform: translateY(80px);
			transition: opacity .8s cubic-bezier(0.34, 0.52, 0.57, 1.04), transform .8s cubic-bezier(0.34, 0.52, 0.57, 1.04);

			&.qodef-item-show {
				opacity: 1;
				transform: translateY(0);

				&.qodef-item-shown {
					transition: none;
				}
			}
			
			.touch & {
				opacity: 1;
				transform: translateY(0);
			}
		}
	}

	/***** Additional Features - end *****/
}

/* ==========================================================================
   Property shortcode style - end
   ========================================================================== */

/* ==========================================================================
   Property filter style - begin
   ========================================================================== */

.qodef-pl-filter-holder {
    @include qodefRelativeHolderLayout();
    margin: 0 0 80px;
    text-align: center;
	font-size: 0;

    ul {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        margin: 0;
        padding: 0;
        list-style: none;

        li {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            margin: 0 0 10px;
	        padding: 0 22px;
            cursor: pointer;


			&:after {
				content: '';
				display: block;
				height: 2px;
				width: 101%;
				position: absolute;
				opacity: 1;
				left: 0;
				bottom: -12px;
				background-color: #d6d6d6;
				@include qodefTransition(width 0.2s ease-in-out);
			}
			&:before {
				content: '';
				display: block;
				height: 2px;
				width: 0%;
				position: absolute;
				opacity: 0;
				z-index: 999;
				left: 0;
				bottom: -12px;
				background-color: #000;
				@include qodefTransition(width 0.2s ease-in-out);
			}
	        
	        @include laptop-landscape-large {
		        padding: 0 16px;
	        }
	
	        @include ipad-landscape {
		        padding: 0 10px;
				margin: 20px 0;
	        }

            span {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                color: #000000;
				font-family: $default-heading-font;
                font-size: 16px;
                line-height: 35px;
				font-weight: 400;
				letter-spacing: 0.25em;
				text-transform: uppercase;
                white-space: nowrap;
				transition: color .2s ease-out;
				transform: translateX(2px);
            }

            &.qodef-pl-current,
            &:hover {
				&:before {
					width: 101%;
					opacity: 1;
				}

            }
        }
    }
}
/* ==========================================================================
   Property filter style - end
   ========================================================================== */

/* ==========================================================================
   Property standard pagination style - begin
   ========================================================================== */

.qodef-property-list-holder {
	
	&.qodef-pl-pag-standard {
		
		.qodef-pl-inner {
			opacity: 1;
			transition: opacity .2s ease-out;
		}
		
		&.qodef-pl-pag-standard-animate {
			
			.qodef-pl-inner {
				opacity: 0;
			}
		}
	}
}

.qodef-pl-standard-pagination {
	@include qodefRelativeHolderLayout();
	margin: 20px 0 0;
	clear: both;
	
	ul {
		@include qodefRelativeHolderLayout();
		padding: 0;
		margin: 0;
		list-style: none;
		text-align: center;
		
		li {
			position: relative;
			display: inline-block;
			vertical-align: top;
			margin: 0 12px;
			padding: 0;
			
			a {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				margin: 0;
				padding: 0;
			}
			
			&.qodef-pl-pag-active {
				
				a {
					color: $first-main-color;
				}
			}
			
			&.qodef-pl-pag-prev,
			&.qodef-pl-pag-next {
				position: absolute;
				top: 0;
				
				a {
					font-size: 30px;
					transition: color .2s ease-out, opacity .2s ease-out;
					
					span {
						display: block;
						line-height: inherit;
						
						&:before {
							display: block;
							line-height: inherit;
						}
					}
				}
			}
			
			&.qodef-pl-pag-prev {
				left: 0;
				
				a {
					opacity: 0;
				}
			}
			
			&.qodef-pl-pag-next {
				right: 0;
				
				a {
					opacity: 1;
				}
			}
		}
	}
}
/* ==========================================================================
   Property standard pagination style - end
   ========================================================================== */

/* ==========================================================================
   Property load more pagination style - begin
   ========================================================================== */

.qodef-pl-load-more-holder {
	@include qodefRelativeHolderLayout();

    .qodef-pl-load-more {
	    margin: 40px 0 0;
	    text-align: center;
    }
}
/* ==========================================================================
   Property load more pagination style - end
   ========================================================================== */

/* ==========================================================================
   Property loading element style - begin
   ========================================================================== */

.qodef-pl-loading {
	position: relative;
	display: none;
	width: 100%;
	margin: 40px 0 20px;
	color: $default-heading-color;
	text-align: center;

	&.qodef-filter-trigger {
		position: absolute;
		top: 250px;
		left: 0;
	}
	
    &.qodef-standard-pag-trigger {
		position: absolute;
		top: 50px;
		left: 0;
	
	    .qodef-pl-has-filter & {
		    top: 150px;
	    }
    }

	&.qodef-showing {
		display: block;
	}

	> div {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		width: 7px;
		height: 7px;
		margin: 0 3px;
		background-color: $default-heading-color;
		border-radius: 100%;
	}

	.qodef-pl-loading-bounce1 {
		-webkit-animation-delay: -0.32s;
		-moz-animation-delay: -0.32s;
		animation-delay: -0.32s;
	}

	.qodef-pl-loading-bounce2 {
		-webkit-animation-delay: -0.16s;
		-moz-animation-delay: -0.16s;
		animation-delay: -0.16s;
	}
}

@-webkit-keyframes sk-bouncedelay {
	0%, 80%, 100% {
		-webkit-transform: scale(0);
	}
	40% {
		-webkit-transform: scale(1.0);
	}
}

@-moz-keyframes sk-bouncedelay {
	0%, 80%, 100% {
		-moz-transform: scale(0);
	}
	40% {
		-moz-transform: scale(1.0);
	}
}

@keyframes sk-bouncedelay {
	0%, 80%, 100% {
		-webkit-transform: scale(0);
		transform: scale(0);
	}
	40% {
		-webkit-transform: scale(1.0);
		transform: scale(1.0);
	}
}
/* ==========================================================================
   Property loading element style - end
   ========================================================================== */